.toggle-menu-wrap {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 6000;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	height: 100%;
	/* -webkit-transform: translate(-78%, 0);
	transform: translate(-78%, 0); */
	-webkit-transform: translate(-83%, 0);
	transform: translate(-83%, 0);
	-webkit-transition: -webkit-transform 0.2s ease-in;
	transition: -webkit-transform 0.2s ease-in;
	transition: transform 0.2s ease-in;
	transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
}
.toggle-menu-wrap.active {
	-webkit-transform: translate(0, 0);
	transform: translate(0, 0);
}

.toggle-menu__btn {
	-webkit-transform: rotate(-90deg);
	transform: rotate(-90deg);
}

.toggle-menu {
	position: relative;
	font-size: 13px;
	color: #ffffff;
	text-transform: uppercase;
	background-color: transparent;
	-webkit-transform-origin: 75px 75px;
	transform-origin: 75px 75px;
	/* -webkit-transform: rotate(-90deg) translate(90%);
	transform: rotate(-90deg) translate(90%); */
}
.toggle-menu span:nth-child(1) {
	position: absolute;
	top: -6px;
	left: 40px;
	display: block;
	width: 46px;
	height: 3px;
	border-radius: 10px;
	background: linear-gradient(246deg, #df0e77 0.76%, #390796 99.1%);
}
.toggle-menu:hover span {
	-webkit-animation-name: animButtonMenu;
	animation-name: animButtonMenu;
	-webkit-animation-timing-function: ease-in;
	animation-timing-function: ease-in;
	-webkit-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
}
.toggle-menu:hover span:nth-child(2) {
	-webkit-animation-delay: 0.1s;
	animation-delay: 0.1s;
}
.toggle-menu:hover span:nth-child(3) {
	-webkit-animation-delay: 0.2s;
	animation-delay: 0.2s;
}
.toggle-menu span {
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}
.toggle-menu span:nth-child(2) {
	position: absolute;
	top: 7px;
	left: 60px;
	display: block;
	width: 46px;
	height: 3px;
	border-radius: 10px;
	background: linear-gradient(246deg, #df0e77 0.76%, #390796 99.1%);
}
.toggle-menu span:nth-child(3) {
	position: absolute;
	top: 20px;
	left: 30px;
	display: block;
	width: 46px;
	height: 3px;
	border-radius: 10px;
	background: linear-gradient(246deg, #df0e77 0.76%, #390796 99.1%);
}
.toggle-menu-wrap.fixed .toggle-menu {
	font-weight: bold;
	color: #df0e77;
}
.toggle-menu.open-menu span:nth-child(1) {
	-webkit-transform: rotate(45deg) translate(4px, 14px);
	transform: rotate(45deg) translate(4px, 14px);
}
.toggle-menu.open-menu span:nth-child(2) {
	opacity: 0;
}
.toggle-menu.open-menu span:nth-child(3) {
	-webkit-transform: rotate(-45deg) translate(5px, -14px);
	transform: rotate(-45deg) translate(5px, -14px);
}
@media (max-width: 1000px) {
	.toggle-menu-wrap {
		display: none !important;
	}
}
.mobile-menu {
	position: relative;
	display: none;
	-webkit-box-flex: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
	width: 45px;
	/* height: 22px; */
	background-color: transparent;
	cursor: pointer;

	margin-right: 10px;
}

.mobile-menu span {
	display: block;
	width: 100%;
	box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
	border-radius: 3px;
	height: 3px;
	background: linear-gradient(246deg, #df0e77 0.76%, #390796 99.1%);
	position: relative;
}

.mobile-menu span + span {
	margin-top: 8px;
}

@keyframes top {
	0% {
		top: 0;
		transform: rotate(0);
	}
	50% {
		top: 9px;
		transform: rotate(0);
	}
	100% {
		top: 9px;
		transform: rotate(45deg);
	}
}

@keyframes top-2 {
	0% {
		top: 9px;
		transform: rotate(45deg);
	}
	50% {
		top: 9px;
		transform: rotate(0deg);
	}
	100% {
		top: 0;
		transform: rotate(0deg);
	}
}

@keyframes bottom {
	0% {
		bottom: 0;
		transform: rotate(0);
	}
	50% {
		bottom: 13px;
		transform: rotate(0);
	}
	100% {
		bottom: 13px;
		transform: rotate(135deg);
	}
}

@keyframes bottom-2 {
	0% {
		bottom: 13px;
		transform: rotate(135deg);
	}
	50% {
		bottom: 13px;
		transform: rotate(0);
	}
	100% {
		bottom: 0;
		transform: rotate(0);
	}
}

@keyframes scaled {
	50% {
		transform: scale(0);
	}
	100% {
		transform: scale(0);
	}
}

@keyframes scaled-2 {
	0% {
		transform: scale(0);
	}
	50% {
		transform: scale(0);
	}
	100% {
		transform: scale(1);
	}
}

