.wave-wrapper {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	z-index: 4;
	margin: auto;
	overflow: hidden;
}
.wave-wrapper-inner {
	position: absolute;
	width: 100%;
	overflow: hidden;
	height: 100%;
	bottom: -1px;
	background-image: -webkit-gradient(linear, left top, left bottom, from(0), color-stop(11.92%, #df0e77), color-stop(85.25%, #390796));
	background-image: linear-gradient(0, #df0e77 11.92%, #390796 85.25%);
}
.bg-top {
	z-index: 15;
	opacity: 0.5;
}
.bg-middle {
	z-index: 10;
	opacity: 0.75;
}
.bg-bottom {
	z-index: 5;
}
.wave {
	position: absolute;
	left: 0;
	width: 200%;
	height: 100%;
	background-repeat: repeat no-repeat;
	background-position: 0 bottom;
	-webkit-transform-origin: center bottom;
	transform-origin: center bottom;
}
.wave-top {
	background-size: 50% 100px;
}
.wave-animation .wave-top {
	animation: move-wave 3s;
	-webkit-animation: move-wave 3s;
	-webkit-animation-delay: 1s;
	animation-delay: 1s;
}
.wave-middle {
	background-size: 50% 120px;
}
.wave-animation .wave-middle {
	-webkit-animation: move_wave 10s linear infinite;
	animation: move_wave 10s linear infinite;
}
.wave-bottom {
	background-size: 50% 100px;
}
.wave-animation .wave-bottom {
	-webkit-animation: move_wave 15s linear infinite;
	animation: move_wave 15s linear infinite;
}
