	content: "";
	display: block;
	height: 5px;
	margin-top: -4px;
	pointer-events: none;
	position: absolute;
	right: 31px;
	top: 31px;
	transform-origin: 66% 66%;
	transform: rotate(225deg);
	transition: all 0.15s ease-in-out;
	width: 10px;
	height: 10px;
	border-bottom: 2px solid #fff;
	border-right: 2px solid #fff;
}

